@import '../../../sass/global.scss';
.navbar-container {
  width: 100%;
  height: 70px;
  background-color: #f0f8ff;
  color: $mainColor;
  position: fixed;
  z-index: 5;
  .wrapper {
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 30px;

    .left {
        display: flex;
        justify-content: center;
        .logo {
            
            font-size: 40px;
            font-weight: 700;
            text-decoration: line-through;
            color: inherit;
            
        }
        .item-container {
            display: flex;
            align-items: center;
            margin-left: 30px;
            .icon{font-size: 18px;
            margin-right: 5px;
        }
            span{
                font-size: 15px;
                font-weight: 500;
            }
        }

    }
    .right{
    .hamburger{
        width: 32px;
        height: 25px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        cursor: pointer;
         
        span{
              
            width: 100%;
            height: 3px;
            background-color: $mainColor;
            transform-origin: left;
            transition: all 2s ease-in-out;

        }
    }
  }
}
  &.active{
    background-color: $mainColor;
    color: aliceblue;
    .hamburger{     
        span{    
            &:first-child{
                background-color: #f0f8ff;
                transform: rotate(45deg);
            }   
            &:nth-child(2){
                opacity: 0;
            }
            &:last-child{
                background-color: #f0f8ff;
                transform: rotate(-45deg);
            }     
             

        }
    }
  }
}
